import { db, Timestamp, COLLECTIONS } from '@/lib/firebase';
import { Notification, User, Invoice, UserBehaviorProfile } from '@/types/models';
import { sendNotificationEmail } from '@/lib/sendgrid';
import { logDbOperation, logInfo, logError } from '@/utils/logger';
import { nanoid } from 'nanoid';

/**
 * Detect invoice drought (user hasn't sent an invoice in X days)
 */
export async function detectInvoiceDroughts(): Promise<{
  notificationsSent: number;
  errors: string[];
}> {
  const startTime = Date.now();
  const errors: string[] = [];
  let notificationsSent = 0;

  try {
    // Get all active users
    const usersSnapshot = await db
      .collection(COLLECTIONS.USERS)
      .where('isActive', '==', true)
      .get();

    logDbOperation('detect_invoice_droughts', COLLECTIONS.USERS, undefined, 0);

    for (const userDoc of usersSnapshot.docs) {
      try {
        const user = userDoc.data() as User;

        // Check notification preferences
        if (!user.notificationPreferences?.invoiceDroughtReminder) {
          continue;
        }

        // Get user's latest invoice
        const latestInvoiceSnapshot = await db
          .collection(COLLECTIONS.INVOICES)
          .where('freelancerId', '==', user.userId)
          .orderBy('createdAt', 'desc')
          .limit(1)
          .get();

        // Calculate days since last invoice
        let daysSinceLastInvoice = 999;
        if (!latestInvoiceSnapshot.empty) {
          const latestInvoice = latestInvoiceSnapshot.docs[0].data() as Invoice;
          const lastInvoiceDate = latestInvoice.createdAt.toDate();
          daysSinceLastInvoice = Math.floor(
            (Date.now() - lastInvoiceDate.getTime()) / (1000 * 60 * 60 * 24)
          );
        }

        // Check if drought detected (14 days default)
        const droughtThreshold = 14;
        if (daysSinceLastInvoice >= droughtThreshold) {
          // Check if we already sent a notification recently
          const recentNotificationSnapshot = await db
            .collection(COLLECTIONS.NOTIFICATIONS)
            .where('userId', '==', user.userId)
            .where('type', '==', 'invoice_drought')
            .where('createdAt', '>', Timestamp.fromDate(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000)))
            .limit(1)
            .get();

          if (recentNotificationSnapshot.empty) {
            logInfo('Invoice drought detected', { userId: user.userId, daysSinceLastInvoice });

            // Create notification
            const notification: Notification = {
              notificationId: nanoid(),
              userId: user.userId,
              type: 'invoice_drought',
              title: "It's been a while since your last invoice",
              message: `You haven't sent an invoice in ${daysSinceLastInvoice} days. Take a moment to check if you have any outstanding work to invoice!`,
              read: false,
              actionUrl: `${process.env.NEXT_PUBLIC_APP_URL}/dashboard/invoices/new`,
              createdAt: Timestamp.now(),
            };

            await db.collection(COLLECTIONS.NOTIFICATIONS).add(notification);

            // Send email if email notifications enabled
            if (user.notificationPreferences?.emailNotifications && user.email) {
              await sendNotificationEmail({
                toEmail: user.email,
                subject: notification.title,
                message: notification.message,
                actionUrl: notification.actionUrl,
              });
            }

            notificationsSent++;
          }
        }
      } catch (error) {
        const errorMsg = `Failed to process user ${userDoc.id}: ${error instanceof Error ? error.message : 'Unknown error'}`;
        logError(errorMsg, error);
        errors.push(errorMsg);
      }
    }

    logDbOperation('detect_invoice_droughts', COLLECTIONS.NOTIFICATIONS, undefined, Date.now() - startTime);

    logInfo('Invoice drought detection complete', { notificationsSent });

    return { notificationsSent, errors };
  } catch (error) {
    logError('Invoice drought detection failed', error);
    throw error;
  }
}

/**
 * Detect payment delays (payment taking longer than user's average)
 */
export async function detectPaymentDelays(): Promise<{
  notificationsSent: number;
  errors: string[];
}> {
  const startTime = Date.now();
  const errors: string[] = [];
  let notificationsSent = 0;

  try {
    // Get all active users with behavior profiles
    const usersSnapshot = await db
      .collection(COLLECTIONS.USERS)
      .where('isActive', '==', true)
      .get();

    logInfo('Checking active users for payment delays', { userCount: usersSnapshot.size });

    for (const userDoc of usersSnapshot.docs) {
      try {
        const user = userDoc.data() as User;

        // Check notification preferences
        if (!user.notificationPreferences?.paymentDelayAlert) {
          continue;
        }

        // Get user's behavior profile
        const behaviorSnapshot = await db
          .collection(COLLECTIONS.USER_BEHAVIOR_PROFILE)
          .where('userId', '==', user.userId)
          .limit(1)
          .get();

        if (behaviorSnapshot.empty) {
          continue;
        }

        const behavior = behaviorSnapshot.docs[0].data() as UserBehaviorProfile;
        const avgPaymentDays = behavior.payments?.averageDaysToPayment || 30;

        // Get outstanding invoices
        const outstandingSnapshot = await db
          .collection(COLLECTIONS.INVOICES)
          .where('freelancerId', '==', user.userId)
          .where('status', '==', 'sent')
          .get();

        for (const invoiceDoc of outstandingSnapshot.docs) {
          const invoice = invoiceDoc.data() as Invoice;

          // Skip if sentAt is missing
          if (!invoice.sentAt) continue;

          const daysSinceSent = Math.floor(
            (Date.now() - invoice.sentAt.toDate().getTime()) / (1000 * 60 * 60 * 24)
          );

          // Alert if payment is taking longer than average + 7 days buffer
          const delayThreshold = avgPaymentDays + 7;
          if (daysSinceSent >= delayThreshold) {
            // Check if we already sent a notification for this invoice
            const existingNotificationSnapshot = await db
              .collection(COLLECTIONS.NOTIFICATIONS)
              .where('userId', '==', user.userId)
              .where('type', '==', 'payment_delay')
              .where('metadata.invoiceId', '==', invoice.invoiceId)
              .limit(1)
              .get();

            if (existingNotificationSnapshot.empty) {
              logInfo('Payment delay detected', { invoiceRef: invoice.reference, daysSinceSent });

              // Create notification
              const notification: Notification = {
                notificationId: nanoid(),
                userId: user.userId,
                type: 'payment_delay',
                title: 'Payment taking longer than usual',
                message: `Invoice ${invoice.reference} for ${invoice.clientName} has been pending for ${daysSinceSent} days (${daysSinceSent - avgPaymentDays} days longer than your average). Consider following up.`,
                read: false,
                actionUrl: `${process.env.NEXT_PUBLIC_APP_URL}/dashboard/invoices/${invoice.invoiceId}`,
                metadata: {
                  invoiceId: invoice.invoiceId,
                  invoiceReference: invoice.reference,
                  daysSinceSent,
                },
                createdAt: Timestamp.now(),
              };

              await db.collection(COLLECTIONS.NOTIFICATIONS).add(notification);

              // Send email if email notifications enabled
              if (user.notificationPreferences?.emailNotifications && user.email) {
                await sendNotificationEmail({
                  toEmail: user.email,
                  subject: notification.title,
                  message: notification.message,
                  actionUrl: notification.actionUrl,
                });
              }

              notificationsSent++;
            }
          }
        }
      } catch (error) {
        const errorMsg = `Failed to process user ${userDoc.id}: ${error instanceof Error ? error.message : 'Unknown error'}`;
        logError(errorMsg, error);
        errors.push(errorMsg);
      }
    }

    logDbOperation('detect_payment_delays', COLLECTIONS.NOTIFICATIONS, undefined, Date.now() - startTime);

    logInfo('Payment delay detection complete', { notificationsSent });

    return { notificationsSent, errors };
  } catch (error) {
    logError('Payment delay detection failed', error);
    throw error;
  }
}

/**
 * Detect opportunities (clients who haven't been invoiced in a while)
 */
export async function detectOpportunities(): Promise<{
  notificationsSent: number;
  errors: string[];
}> {
  const startTime = Date.now();
  const errors: string[] = [];
  let notificationsSent = 0;

  try {
    // Get all active users
    const usersSnapshot = await db
      .collection(COLLECTIONS.USERS)
      .where('isActive', '==', true)
      .get();

    logInfo('Checking active users for opportunities', { userCount: usersSnapshot.size });

    for (const userDoc of usersSnapshot.docs) {
      try {
        const user = userDoc.data() as User;

        // Check notification preferences
        if (!user.notificationPreferences?.opportunityAlert) {
          continue;
        }

        // Get all invoices for this user
        const invoicesSnapshot = await db
          .collection(COLLECTIONS.INVOICES)
          .where('freelancerId', '==', user.userId)
          .where('status', '!=', 'cancelled')
          .get();

        // Group by client email
        const clientLastInvoice = new Map<string, { date: Date; clientName: string }>();

        invoicesSnapshot.docs.forEach((doc) => {
          const invoice = doc.data() as Invoice;
          const invoiceDate = invoice.createdAt.toDate();

          const existing = clientLastInvoice.get(invoice.clientEmail);
          if (!existing || invoiceDate > existing.date) {
            clientLastInvoice.set(invoice.clientEmail, {
              date: invoiceDate,
              clientName: invoice.clientName,
            });
          }
        });

        // Check for opportunities (client not invoiced in 60+ days)
        const opportunityThreshold = 60;
        const now = Date.now();

        for (const [clientEmail, lastInvoice] of clientLastInvoice.entries()) {
          const daysSinceLastInvoice = Math.floor(
            (now - lastInvoice.date.getTime()) / (1000 * 60 * 60 * 24)
          );

          if (daysSinceLastInvoice >= opportunityThreshold) {
            // Check if we already sent a notification for this client recently
            const recentNotificationSnapshot = await db
              .collection(COLLECTIONS.NOTIFICATIONS)
              .where('userId', '==', user.userId)
              .where('type', '==', 'opportunity')
              .where('metadata.clientEmail', '==', clientEmail)
              .where('createdAt', '>', Timestamp.fromDate(new Date(now - 30 * 24 * 60 * 60 * 1000)))
              .limit(1)
              .get();

            if (recentNotificationSnapshot.empty) {
              logInfo('Opportunity detected', { clientName: lastInvoice.clientName, daysSinceLastInvoice });

              // Create notification
              const notification: Notification = {
                notificationId: nanoid(),
                userId: user.userId,
                type: 'opportunity',
                title: 'Opportunity: Dormant client',
                message: `You haven't invoiced ${lastInvoice.clientName} in ${daysSinceLastInvoice} days. This might be a good time to reach out and check if they need any work done!`,
                read: false,
                actionUrl: `${process.env.NEXT_PUBLIC_APP_URL}/dashboard/invoices/new?clientEmail=${encodeURIComponent(clientEmail)}`,
                metadata: {
                  clientEmail,
                  clientName: lastInvoice.clientName,
                  daysSinceLastInvoice,
                },
                createdAt: Timestamp.now(),
              };

              await db.collection(COLLECTIONS.NOTIFICATIONS).add(notification);

              // Send email if email notifications enabled
              if (user.notificationPreferences?.emailNotifications && user.email) {
                await sendNotificationEmail({
                  toEmail: user.email,
                  subject: notification.title,
                  message: notification.message,
                  actionUrl: notification.actionUrl,
                });
              }

              notificationsSent++;
            }
          }
        }
      } catch (error) {
        const errorMsg = `Failed to process user ${userDoc.id}: ${error instanceof Error ? error.message : 'Unknown error'}`;
        logError(errorMsg, error);
        errors.push(errorMsg);
      }
    }

    logDbOperation('detect_opportunities', COLLECTIONS.NOTIFICATIONS, undefined, Date.now() - startTime);

    logInfo('Opportunity detection complete', { notificationsSent });

    return { notificationsSent, errors };
  } catch (error) {
    logError('Opportunity detection failed', error);
    throw error;
  }
}

/**
 * Get notifications for a user
 */
export async function getUserNotifications(
  userId: string,
  limit = 20,
  unreadOnly = false
): Promise<Notification[]> {
  const startTime = Date.now();

  let query = db
    .collection(COLLECTIONS.NOTIFICATIONS)
    .where('userId', '==', userId);

  if (unreadOnly) {
    query = query.where('read', '==', false);
  }

  const snapshot = await query
    .orderBy('createdAt', 'desc')
    .limit(limit)
    .get();

  logDbOperation('get_user_notifications', COLLECTIONS.NOTIFICATIONS, undefined, Date.now() - startTime);

  return snapshot.docs.map((doc) => doc.data() as Notification);
}

/**
 * Mark notification as read
 */
export async function markNotificationRead(notificationId: string, userId: string): Promise<void> {
  const startTime = Date.now();

  const snapshot = await db
    .collection(COLLECTIONS.NOTIFICATIONS)
    .where('notificationId', '==', notificationId)
    .where('userId', '==', userId)
    .limit(1)
    .get();

  if (snapshot.empty) {
    throw new Error('Notification not found');
  }

  await snapshot.docs[0].ref.update({
    read: true,
    readAt: Timestamp.now(),
  });

  logDbOperation('mark_notification_read', COLLECTIONS.NOTIFICATIONS, notificationId, Date.now() - startTime);
}

/**
 * Mark all notifications as read for a user
 */
export async function markAllNotificationsRead(userId: string): Promise<number> {
  const startTime = Date.now();

  const snapshot = await db
    .collection(COLLECTIONS.NOTIFICATIONS)
    .where('userId', '==', userId)
    .where('read', '==', false)
    .get();

  const batch = db.batch();
  snapshot.docs.forEach((doc) => {
    batch.update(doc.ref, {
      read: true,
      readAt: Timestamp.now(),
    });
  });

  await batch.commit();

  logDbOperation('mark_all_notifications_read', COLLECTIONS.NOTIFICATIONS, undefined, Date.now() - startTime);

  return snapshot.size;
}
