import { NextRequest, NextResponse } from 'next/server';
import {
  detectInvoiceDroughts,
  detectPaymentDelays,
  detectOpportunities,
} from '@/services/notificationService';
import { logger } from '@/utils/logger';

export const dynamic = 'force-dynamic';
export const maxDuration = 300; // 5 minutes max execution time

/**
 * Send smart notifications (droughts, delays, opportunities)
 * This cron job runs daily at 10am
 * GET /api/cron/send-notifications
 */
export async function GET(req: NextRequest): Promise<NextResponse> {
  const startTime = Date.now();

  try {
    // 1. Verify cron secret for security
    const authHeader = req.headers.get('authorization');
    const cronSecret = process.env.CRON_SECRET;

    if (!cronSecret) {
      logger.error('CRON_SECRET not configured');
      return NextResponse.json({ error: 'Server misconfiguration' }, { status: 500 });
    }

    if (authHeader !== `Bearer ${cronSecret}`) {
      logger.warn('Unauthorized cron job attempt');
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }

    logger.info('Starting smart notifications cron job');

    // 2. Detect invoice droughts
    const droughtResult = await detectInvoiceDroughts();

    // 3. Detect payment delays
    const delayResult = await detectPaymentDelays();

    // 4. Detect opportunities
    const opportunityResult = await detectOpportunities();

    const duration = Date.now() - startTime;

    logger.info('Smart notifications completed', {
      droughtNotifications: droughtResult.notificationsSent,
      delayNotifications: delayResult.notificationsSent,
      opportunityNotifications: opportunityResult.notificationsSent,
      totalErrors: droughtResult.errors.length + delayResult.errors.length + opportunityResult.errors.length,
      duration,
    });

    return NextResponse.json({
      success: true,
      message: 'Smart notifications processed successfully',
      stats: {
        invoiceDroughts: {
          sent: droughtResult.notificationsSent,
          errors: droughtResult.errors,
        },
        paymentDelays: {
          sent: delayResult.notificationsSent,
          errors: delayResult.errors,
        },
        opportunities: {
          sent: opportunityResult.notificationsSent,
          errors: opportunityResult.errors,
        },
        duration,
      },
    });
  } catch (error) {
    const duration = Date.now() - startTime;

    logger.error('Smart notifications failed', {
      error: error instanceof Error ? error.message : 'Unknown error',
      duration,
    });

    return NextResponse.json(
      {
        success: false,
        error: error instanceof Error ? error.message : 'Smart notifications failed',
        duration,
      },
      { status: 500 }
    );
  }
}
